body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.movie-poster {
  transition: all 0.3s ease-in-out;
}

.movie-poster:hover {
  transform: scale(1.02);
}

.movie-title {
  cursor: pointer;
}

.detail-img {
  width: 25%;
  height: 25%;
  min-width: 340px;
  max-width: 400px;
  min-height: 455px;
}

footer {
  margin-top: 50px;
  background: #111;
  height: auto;
  width: 100%;
  padding-top: 10px;
  color: #fff;
}

.footer-content {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  text-align: center;
}

.footer-content h3 {
  font-size: 28px;
  font-weight: 400;
  text-transform: capitalize;
  line-height: 32px;
}

.footer-content p {
  max-width: 500px;
  margin: 10px auto;
  line-height: 28px;
  font-size: 14px;
}

.footer-socials {
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 16px 0 20px 0;
}

.footer-socials li {
  margin: 0 10px;
}

.footer-socials a {
  text-decoration: none;
  color: #fff;
}

.footer-socials a i {
  font-size: 18px;
  transition: color 0.4s ease;
}

.footer-socials a:hover i {
  color: #4b94f4;
}

.footer-bottom {
  background: #000;
  width: 100%;
  padding: 10px 0;
  text-align: center;
}

.footer-bottom p {
  font-size: 14px;
  word-spacing: 2px;
  text-transform: capitalize;
}

.footer-bottom span {
  text-transform: uppercase;
  color: #3487f3;
  font-weight: 200;
}